.mint-toast {
  position: fixed;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  z-index: 1000;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
      flex-direction: column;
  -webkit-box-align: center;
  -ms-flex-align: center;
      align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
      justify-content: center;
  box-sizing: border-box;
  background: rgba(0, 0, 0, 0.7);
  min-width: 4.16rem;
  padding: 0.693rem 0.267rem 0.533rem 0.267rem !important;
  border-radius: 0.053rem;
  font-size: 0.427rem;
}

.mint-toast .icon-success {
  display: inline-block;
  background-image: url(./assets/img/success_ok.png);
  background-size: 1.067rem auto;
  width: 1.067rem;
  height: 1.067rem;
  margin: 0 0 0.427rem 0;
}

.mint-toast .mint-toast-text {
  padding: 0 !important;
  color: #fff;
}

body {
  background-color: #fff;
}


