.parent-tabs-area {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
      align-items: center;
  background: #41B883;
  height: 1.2rem;
  padding: 0 0.32rem;
}

.parent-tabs-area .link-btn {
  display: block;
  background-size: 0.64rem auto;
  width: 0.64rem;
  height: 0.64rem;
}

.parent-tabs-area .icon-collect-link {
  background-image: url(../assets/img/collect_star.png);
}

.parent-tabs-area .icon-portrait {
  background-image: url(../assets/img/portrait.png);
}

.parent-tabs-area .tabs-content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 4.693rem;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.parent-tabs-area .tabs-content .tab-item {
  opacity: .5;
}

.parent-tabs-area .tabs-content .tab-item > a {
  font-size: 0.48rem;
  color: #fff;
}

.parent-tabs-area .tabs-content .tab-item.active {
  opacity: 1;
}

.sub-tabs-area {
  -ms-touch-action: none;
      touch-action: none;
  background-color: #fff;
  -webkit-transform: translate3d(0, 0, 0);
  overflow: hidden;
}

.sub-tabs-area .scroller,
.sub-tabs-area ul.tabs {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  height: 1.067rem;
  -webkit-box-align: center;
  -ms-flex-align: center;
      align-items: center;
  padding: 0 0.32rem;
}

.sub-tabs-area .scroller .tab-item,
.sub-tabs-area ul.tabs .tab-item {
  position: relative;
  height: 100%;
  line-height: 1.067rem;
  margin-right: 0.48rem;
  font-size: 0.427rem;
  color: #666;
  white-space: nowrap;
}

.sub-tabs-area .scroller .tab-item.has-not-read-msgs:before,
.sub-tabs-area ul.tabs .tab-item.has-not-read-msgs:before {
  position: absolute;
  top: 0.24rem;
  right: -0.133rem;
  display: block;
  content: '';
  background-color: #FC4239;
  width: 0.16rem;
  height: 0.16rem;
  border-radius: 100%;
}

.sub-tabs-area .scroller .tab-item.active,
.sub-tabs-area ul.tabs .tab-item.active {
  color: #41B883;
}

.sub-tabs-area .scroller .tab-item.active:after,
.sub-tabs-area ul.tabs .tab-item.active:after {
  position: absolute;
  bottom: 0;
  display: block;
  content: '';
  width: 100%;
  height: 0.08rem;
  background: #41B883;
}

.sub-tabs-area .scroller .tab-item:nth-last-child(1),
.sub-tabs-area ul.tabs .tab-item:nth-last-child(1) {
  margin-right: 0;
}


