.msg-items {
  position: absolute;
  z-index: 1;
  top: 2.267rem;
  bottom: 0;
  overflow: hidden;
  background-color: #F7F7F7;
  width: 100%;
  -ms-touch-action: none;
      touch-action: none;
}

.msg-items .msg-item {
  background: #fff;
  padding: 0.373rem 0.32rem 0.427rem 0.373rem;
  border-top: 0.027rem solid #EFEFEF;
  border-bottom: 0.027rem solid #EFEFEF;
}

.msg-items .msg-item .title {
  color: #333;
  font-size: 0.427rem;
}

.msg-items .msg-item .title:hover {
  color: #0ca0fc;
}

.msg-items .msg-item .company-name {
  margin-top: 0.16rem;
  color: #999;
  font-size: 0.373rem;
}

.msg-items .msg-item .bottom-line {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-top: 0.427rem;
  -webkit-box-align: center;
  -ms-flex-align: center;
      align-items: center;
}

.msg-items .msg-item .bottom-line .type {
  float: left;
  display: inline-block;
  background: #F0F0F0;
  padding: 0.16rem 0.24rem;
  border-radius: 2.667rem;
  color: #41B883;
  font-size: 0.32rem;
}

.msg-items .msg-item .bottom-line .right-part {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: reverse;
  -ms-flex-direction: row-reverse;
      flex-direction: row-reverse;
  -webkit-box-align: center;
  -ms-flex-align: center;
      align-items: center;
}

.msg-items .msg-item .bottom-line .right-part .date {
  float: right;
  margin-right: 0.267rem;
  color: #BDBDBD;
  font-size: 0.32rem;
}

.msg-items .msg-item .bottom-line .right-part .collect-icon {
  float: right;
  background-size: 0.64rem auto;
  width: 0.64rem;
  height: 0.64rem;
}

.msg-items .msg-item .bottom-line .right-part .icon-has-collect {
  background-image: url(../assets/img/has_collect.png);
}

.msg-items .msg-item .bottom-line .right-part .icon-not-collect {
  background-image: url(../assets/img/not_collect.png);
}

.msg-items .data-load-tips {
  padding: 0.4rem 0 0.267rem 0;
  text-align: center;
  color: #999;
  font-size: 0.32rem;
}

.msg-items .date-separation-area {
  height: 0.747rem;
  line-height: 0.747rem;
  text-align: center;
  font-size: 0.32rem;
  color: #BDBDBD;
}

.msg-items .separation-area {
  height: 0.267rem;
}


